import React, { Component } from 'react';
import Footer from '../Footer/Footer';

export default class Intro extends Component {
    render(){
        return(
            <div className="page page-intro">
                <div className="slideIntro">
                    <p className="content-slideIntro"><b>GIỚI THIỆU CHUNG</b></p>
                    <div className="group-text-slideIntro">
                        <p><b>SƠ LƯỢC VỀ CÔNG TY</b></p>
                        <br/>
                        <p>Được thành lập ngày 14/4/2021 ngay sau buổi báo cáo giữa kì ( tiền thân
                           là công ty TNHH Smart Device). Tập đoàn Slender hoạt động trong lĩnh vực
                           dịch vụ Trà Cà phê Việt Nam và xây biệt thự cho thuê. Năm 2029, Tập đoàn
                           tái cấu trúc và hợp nhất các công ty thành 2 Tập đoàn:
                        </p>
                        <br/>
                        <div className="row">
                            <div class="col-xs-5 col-sm-5 col-md-5 col-lg-5">
                                <li className="slender-content"><b>SLENDER HOUSE</b></li>
                                <p className="slender-text">Hoạt động trong lĩnh vực Trà Cà phê Việt Nam và tiến đến thiết lập
                                                            chuỗi hệ thống cung cấp dịch vụ an toàn, vệ sinh và mang lại cho mỗi
                                                            người đến với Slender House một niềm vui nho nhỏ.
                                </p>
                            </div>
                            <div class="col-xs-2 col-sm-2 col-md-2 col-lg-2"></div>
                            <div class="col-xs-5 col-sm-5 col-md-5 col-lg-5">
                                <li className="slender-content"><b>SLENDER LAND</b></li>
                                <p className="slender-text">
                                    Hoạt động trong lĩnh vực bất động sản.
                                </p>
                            </div>
                        </div>
                        <br/>
                        <p>Hiện nay, Slender Group là một trong những công ty có uy tín 
                            trong lĩnh vực Đầu tư và Phát triển Bất động sản và đang chờ được qua môn.</p>
                    </div>
                </div>
                <Footer pageFooter={'introFooter'}/>
            </div>
        );
    }
}