import React, { Component } from 'react';
import ItemProduct from './ItemProducts';
import Footer from '../../Footer/Footer';

export default class ListProductVilla extends Component {
    constructor(props){
        super(props);
        this.state = {
            API: 'http://localhost:5000/api/du-an-bat-dong-san-nghi-duong',
            isFind: true,
            keyWord: ""
        }
    }
    onHandleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }
    render() {
        var {API, isFind, keyWord} = this.state;
        return (
            <div className="page">
                <div className="bds-nha-o">
                    <h3>BẤT ĐỘNG SẢN KHU NGHỈ DƯỠNG</h3>
                    <hr className="hr-style" />
                    <div className="row bds-text">
                        <div class="col-xs-4 col-sm-4 col-md-4 col-lg-4"></div>
                        <div className="col-xs-4 col-sm-4 col-md-4 col-lg-4 form-text-bds" style={{backgroundColor: "green"}}>
                                <b className="bds-text-1">Dự án đã hoàn thành</b>
                        </div>
                        <div class="col-xs-4 col-sm-4 col-md-4 col-lg-4"></div>
                    </div>
                    <div className="d-flex form-search">
                        <b>Tên sản phẩm</b>
                        <input 
                            id="keyword-text"
                            type="text"
                            name="keyWord" 
                            className="form-search-bds" 
                            value={keyWord}
                            onChange={this.onHandleChange}
                            required/>
                        <button type="submit" className="btn-search-bds"><i class="fa fa-search" aria-hidden="true"></i></button>
                    </div>
                    <div >
                        <ItemProduct API={API} status={'no'} find={isFind} keyword={keyWord}/>
                    </div>
                </div>
                <Footer pageFooter={'listVillaFooter'}/>
            </div>
        );
    }
}