import React, { Component } from 'react';
import axios from 'axios';

export default class ItemProduct extends Component {
    state = {
        products: []
    }
    async componentDidMount() {
        axios.get(this.props.API)
            .then(res => {
                const products = this.props.status === 'no' ? res.data : res.data.product;
                this.setState({ products });
            })
    }
    render() {
        const {keyword} = this.props;
        return (
            <div className="row form-item-product">
                {this.state.products.map(item => {
                    if (this.props.find === true){
                        if ((item.status === this.props.build || this.props.status === 'no') && item.name.toLowerCase().includes(keyword.toLowerCase())){
                            return (
                                <a className="col-xs-4 col-sm-4 col-md-4 col-lg-4 item-product mt-15" href={item.link}>
                                    <img className="img-product" src={item.image} />
                                    <p className="text-product">{item.name}</p>
                                </a>
                            );
                        }
                    }
                    else{
                        if (item.status === this.props.build || this.props.status === 'no') {
                            return (
                                <a className="col-xs-4 col-sm-4 col-md-4 col-lg-4 item-product mt-15" href={item.link}>
                                    <img className="img-product" src={item.image} />
                                    <p className="text-product">{item.name}</p>
                                </a>
                            );
                        }
                    }
                })}
            </div>
        );
    }
}
