import React, { Component } from 'react';
import Footer from '../../Footer/Footer';
import ItemProduct from './ItemProducts';

export default class ListProductHouse extends Component {
    constructor(props){
        super(props);
        this.state = {
            isBuilding: false,
            API: "http://localhost:5000/api/du-an-bat-dong-san",
            isFind: true,
            keyWord: ""
        }
    }
    onBuilding = () =>{
        this.setState({isBuilding: true})
    }
    onNoBuilding = () => {
        this.setState({isBuilding: false})
    }
    onHandleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }
    render() {
        var {isBuilding, API, isFind, keyWord} = this.state;
        return (
            <div className="page">
                <div className="bds-nha-o">
                    <h3>BẤT ĐỘNG SẢN NHÀ Ở</h3>
                    <hr className="hr-style" />
                    <div className="row bds-text">
                        <div className="col-xs-4 col-sm-4 col-md-4 col-lg-4"></div>
                        <div className="col-xs-2 col-sm-2 col-md-2 col-lg-2 form-text-bds" 
                            style={isBuilding ? {backgroundColor: "green"} : {}}
                            onClick={this.onBuilding}>
                                <b className="bds-text-1">Dự án đã hoàn thành</b>
                        </div>
                        <div className="col-xs-2 col-sm-2 col-md-2 col-lg-2 form-text-bds" 
                            style={isBuilding ? {} : {backgroundColor: "green"}}
                            onClick={this.onNoBuilding}>
                            <b className="bds-text-1">Dự án đang phát triển</b>
                        </div>
                        <div className="col-xs-4 col-sm-4 col-md-4 col-lg-4"></div>
                    </div>
                    <div className="d-flex form-search">
                        <b>Tên sản phẩm</b>
                        <input 
                            id="keyword-text"
                            type="text"
                            name="keyWord" 
                            className="form-search-bds" 
                            value={keyWord}
                            onChange={this.onHandleChange}
                            required/>
                        <button type="submit" className="btn-search-bds"><i class="fa fa-search" aria-hidden="true"></i></button>
                    </div>
                    <div >
                        <ItemProduct build={isBuilding} API={API} find={isFind} keyword={keyWord}/>
                    </div>
                </div>
                <Footer pageFooter={isBuilding ? 'listHouseFooter1' : 'listHouseFooter2'}/>
            </div>
        );
    }
}