import React, { Component } from 'react';
import Footer from '../Footer/Footer';

export default class HinhAnh extends Component {
    render(){
        return(
            <div className="page page-hinhanh">
                <h2 className="title-img">1 SỐ HÌNH ẢNH CÁC DỰ ÁN</h2>
                <img className="img-hinh-anh-1" src='http://chungcuvip.biz/chungcuvip/upload/images/mipec-xuan-thuy.jpeg' />
                <img className="img-hinh-anh-2" src='https://recfu.com/wp-content/uploads/2018/04/Riviera-Point.jpg' />
                <Footer pageFooter={'hinhanh-Footer'}/>
            </div>
        );
    }
}