import React, { Component } from 'react';
import FormRegister from './FormRegister';

export default class RegisterButton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            onShowRegister: false,
        }
    }
    showRegister = () => {
        this.setState({ onShowRegister: !this.state.onShowRegister })
    }
    render() {
        var {onShowRegister} = this.state;
        return (
            <div>
                {onShowRegister ? <FormRegister/> : ""}
                <div className="btn-register btn-support" 
                >
                    <i onClick={this.showRegister} className="fa fa-bookmark" aria-hidden="true"></i>
                    <p onClick={this.showRegister} className="text-support ml-15">Đăng kí</p>
                </div>
            </div>
        );
    }
}