import React, { Component } from 'react';
import axios from 'axios';
import BackGround from '../../images/background-register.png';

export default class MessForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            name: "",
            phone: "",
            message: "",
            mail: ""
        };
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };
    submitHandler = async (e) => {
        e.preventDefault();
        console.log(this.state);
        await axios
            .post("http://localhost:5000/api/schedule", this.state)
            .then((res) => {
                console.log(res);
            })
            .catch((err) => {
                console.log(err);
            });
        alert('Tin nhắn đã được gửi')
        this.setState({ name: "", phone: "", message: "", mail: "" })
    };


    render() {
        const { name, phone, message, mail } = this.state
        return (
            <div className="form-register">
                <img className="img-register" src={BackGround} />
                <div className="title-register">
                    <b>ĐẶT LỊCH XEM NHÀ MẪU</b>
                </div>
                <p className="note-register">Quý khách vui lòng điền đầy đủ thông tin bên dưới để đặt lịch xem nhà mẫu<br />
                   &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;và nhận được thông tin chính thức của chủ đầu tư về dự án<br />
                   &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Slender Land và các chương trình khuyến mãi.
                   <div className="input-register ml-20">
                        <form onSubmit={this.submitHandler}>
                            <input
                                id="ip-name"
                                type="text"
                                className="form-control"
                                name="name"
                                value={name}
                                onChange={this.changeHandler}
                                placeholder="Name"
                                required />
                            <input
                                id="ip-mail"
                                type="text"
                                className="form-control"
                                name="mail"
                                value={mail}
                                onChange={this.changeHandler}
                                placeholder="Mail"
                                required />
                            <input
                                id="ip-phone"
                                type="text"
                                className="form-control"
                                name="phone"
                                value={phone}
                                onChange={this.changeHandler}
                                placeholder="Phone"
                                pattern="(84|0[3|5|7|8|9])+([0-9]{8})\b"
                                required />
                            <input
                                id="ip-note"
                                type="text"
                                className="form-control input-mess"
                                name="message"
                                value={message}
                                onChange={this.changeHandler}
                                placeholder="Massage"
                                required />
                            <input
                                id="btn-register"
                                name="submit-btn"
                                className="btn btn-success"
                                type="submit"
                                value="Đăng kí"
                            />
                        </form>
                    </div>
                </p>
            </div>
        );
    }
}