import React, { Component } from 'react';
import axios from 'axios';

export default class MessForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            name: "",
            phone: "",
            message: "",
        };
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };
    submitHandler = async (e) => {
        e.preventDefault();
        //console.log(this.state);
        await axios
            .post("http://localhost:5000/api/contact", this.state)
            .then((res) => {
                //console.log(res);
            })
            .catch((err) => {
                //console.log(err);
            });
        alert('Tin nhắn đã được gửi')
        this.setState({name: "", phone: "", message: ""})
    };


    render() {
        const { name, phone, message } = this.state
        return (
            <div className="message-form">
                <div className="mess-form-title">
                    <h6 className="mt-15 ml-15">Send message</h6>
                    <br />
                    <p className="note-mess">Tư vấn hiện tại vắng mặt. Xin vui lòng để lại tin nhắn để chúng tôi trực tiếp liên lạc lại quý khách.</p>
                </div>
                <div className="mess-form-input">
                    <div className="form-group-input">
                        <form onSubmit={this.submitHandler}>
                            <input
                                id="ip-name"
                                type="text"
                                className="form-control"
                                name="name"
                                value={name}
                                onChange={this.changeHandler}
                                placeholder="Name"
                                required />
                            <input
                                id="ip-phone"
                                type="text"
                                className="form-control"
                                name="phone"
                                value={phone}
                                onChange={this.changeHandler}
                                placeholder="Phone"
                                pattern="(84|0[3|5|7|8|9])+([0-9]{8})\b"
                                required />
                            <input
                                id="ip-note"
                                type="text"
                                className="form-control input-mess"
                                name="message"
                                value={message}
                                onChange={this.changeHandler}
                                placeholder="Massage"
                                required />
                            <input
                                name="submit-btn"
                                className="btn btn-success"
                                type="submit"
                                value="Hoàn tất"
                            />
                        </form>
                    </div>
                    <h6>By PK Solution</h6>
                </div>
            </div>
        );
    }
}