import React, { Component } from 'react';
import axios from 'axios';

export default class Footer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            email: "",
        };
    }
    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    submitHandler = async (e) => {
        e.preventDefault();
        // console.log(this.state);
        await axios
            .post("http://localhost:5000/api/sign-up", this.state)
            .then((res) => {
                console.log(res);
            })
            .catch((err) => {
                console.log(err);
            });
        alert('Đăng kí thành công')
        this.setState({email: ""});
    };
    render() {
        const { email } = this.state;
        const { pageFooter } = this.props;
        return ( 
            <div className={'footer' + ' ' + this.props.pageFooter}>
                <div className="footer-top mt-15">
                    <div className="row ml-150 mr-150">
                        <div className="col-4">
                            <h5>Đăng kí nhận tin</h5>
                            <p>Hãy kết nối với chúng tôi để theo dõi thông tin mới nhất</p>
                            <div className="group-mail mt-15">
                                <form onSubmit={this.submitHandler}>
                                    <input
                                        name="email"
                                        value={email}
                                        id="mail-input"
                                        className="btn btn-default"
                                        type="text"
                                        placeholder="Địa chỉ Email"
                                        pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                                        onChange={this.changeHandler}
                                        required
                                    />
                                    <span>
                                        <input
                                            name="submit"
                                            id="btn-submit"
                                            className="btn btn-success ml-15"
                                            type="submit"
                                            value="Đăng kí"
                                        />
                                    </span>
                                </form>
                            </div>
                        </div>
                        <div className="col-4">
                            <h5>Brochure Công ty</h5>
                        </div>
                        <div className="col-4">
                            <p><b>Tổng đài CSKH: 0967312210</b></p>
                            <p><b>Email: testandsave@gmail.com</b></p>
                        </div>
                    </div>
                </div>
                <div className="footer-bot mt-300 ">
                    <div className="row ml-150 mr-150">
                        <div className="col-6">
                            <h4>THÔNG TIN LIÊN HỆ</h4>
                            <br />
                            <b>CÔNG TY CỔ PHẦN TẬP ĐOÀN ĐẦU TƯ ĐỊA ỐC SLENDER</b>
                            <div className="d-flex mt-15">
                                <i class="fa fa-home" aria-hidden="true"></i>
                                <p>Trụ sở: 280 An Dương Vương, P.8, Q.5</p>
                            </div>
                            <div className="d-flex">
                                <i class="fa fa-location-arrow" aria-hidden="true"></i>
                                <p>Chi nhánh: Tầng 1, Tòa nhà A, 351A Lạc Long Quân, P.5, Q.11</p>
                            </div>
                        </div>
                        <div className="col-2">

                        </div>
                        <div className="col-4">
                            <h4>KẾT NỐI CHÚNG TÔI</h4>
                            <br />
                            <div className="d-flex">
                                <i className="fa fa-facebook-official icon-footer" aria-hidden="true"></i>
                                <i className="fa fa-twitter-square icon-footer ml-20" aria-hidden="true"></i>
                                <i className="fa fa-google-plus-square icon-footer ml-20" aria-hidden="true"></i>
                            </div>
                            <br />
                            <p>&copy; 4/5/2021 Slender Land. All rights reserved.</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}