import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NavHeader from './Nav-Header';
import ContentBox from '../ContentBox';
import logo from '../../logo.png';


class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShowContent: true
        }
    }
    showContent = () => {
        this.setState({ isShowContent: true })
    }
    hideContent = () => {
        this.setState({ isShowContent: false })
    }
    render() {
        return (
            <div className="container-fluid d-flex header">
                <Link to="/" onClick={this.showContent}><img src={logo} className="img-logo" /></Link>
                <nav className="nav mt-30 navHeader">
                    <Link onClick={this.showContent} to="/" className="nav-link active"><i class="fa fa-home" aria-hidden="true"></i></Link>
                    <Link onClick={this.showContent} to="/intro" className="nav-link">Giới Thiệu</Link>
                    <Link onClick={this.showContent} to="/vi-tri" className="nav-link">Vị Trí</Link>
                    <Link onClick={this.showContent} to="/du-an" className="nav-link">Dự Án</Link>
                    <Link onClick={this.showContent} to="/tien-ich" className="nav-link">Tiện Ích</Link>
                    <Link onClick={this.showContent} to="/phat-trien-ben-vung" className="nav-link">Phát Triển Bền Vững</Link>
                    <Link onClick={this.showContent} to="/tuyen-dung" className="nav-link">Tuyển Dụng</Link>
                    <Link onClick={this.showContent} to="/tin-tuc" className="nav-link">Tin Tức</Link>
                    <Link onClick={this.showContent} to="/hinh-anh" className="nav-link">Hình Ảnh</Link>
                    <Link onClick={this.showContent} to="/lien-he" className="nav-link">Liên Hệ</Link>
                </nav>
                <div className="hotline mt-30 d-flex">
                    <i className="fa fa-phone" aria-hidden="true"></i>
                    <p className="phone"><b>0967312210</b></p>
                </div>
                {this.state.isShowContent ?
                    <div><ContentBox />
                        <div className="btn-exit" onClick={this.hideContent}>
                            <i class="fa fa-times icon-content" aria-hidden="true"></i>
                        </div></div>
                    : ""}
            </div>
        );
    }
}

export default Header;