import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class LibraryLink extends Component{
    render(){
        return(
            <Link to="/hinh-anh" className="btn-library btn-support">
                <i class="fa fa-image"></i>
                <p className="text-support ml-15">Thư Viện</p>
            </Link>
        );    
    }
}