import React, { Component } from 'react';
import MessForm from './MessageForm'; 

export default class MessageButton extends Component {
    constructor(props){
        super(props);
        this.state = {
            isShowForm: false
        }
    }
    onShowForm = () =>{
        this.setState({
            isShowForm: !this.state.isShowForm
        })
    }
    render(){
        var { isShowForm } = this.state;
        var elmShowForm = isShowForm ? <MessForm/> : "";
        var elmChangeIcon = isShowForm ? <i class="fa fa-times" aria-hidden="true"></i> : <i class="fa fa-commenting-o" aria-hidden="true"></i>;
        return(
            <div>
                {elmShowForm}
                <div className="message-button" onClick={this.onShowForm}>
                    <a className="mess-icon" style={{ display: "block" }}>
                        {elmChangeIcon}
                    </a>
                </div>
            </div>
        );
    }
}