import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom';
import Main from './components/Main';
import Home from './components/Page/Home';
import Intro from './components/Page/Intro';
import ViTri from './components/Page/Location';
import DuAn from './components/Page/DuAn';
import PhatTrienBV from './components/Page/PhatTrienBV';
import TienIch from './components/Page/TienIch';
import TuyenDung from './components/Page/TuyenDung';
import HinhAnh from './components/Page/HinhAnh';
import TinTuc from './components/Page/TinTuc';
import LienHe from './components/Page/LienHe';
import ListProductHouse from './components/Page/Products/ListProductHouse';
import ListProductVilla from './components/Page/Products/ListProductVilla';

export default class App extends Component {
    render() {
        return (    
            <Router>
                <Route to="/" exact><Main/></Route>
                <Switch>
                    <Route path="/" exact><Home/></Route>
                    <Route path="/intro"><Intro/></Route>
                    <Route path="/vi-tri"><ViTri/></Route>
                    <Route exact path='/du-an'><DuAn/></Route>
                    <Route path='/du-an/nha-o'><ListProductHouse/></Route>
                    <Route path='/du-an/villa'><ListProductVilla/></Route>
                    <Route path="/phat-trien-ben-vung"><PhatTrienBV/></Route>
                    <Route path="/tien-ich"><TienIch/></Route>
                    <Route path="/tuyen-dung"><TuyenDung/></Route>
                    <Route path="/hinh-anh"><HinhAnh/></Route>
                    <Route path="/tin-tuc"><TinTuc/></Route>
                    <Route path="/lien-he"><LienHe/></Route>
                </Switch>
            </Router>
        );
    }
}

