import React from 'react';
import {Link} from 'react-router-dom';
import ContentBox from "../ContentBox";

function NavHeader(props){

    return(
        <div>
            {props.show ? <ContentBox/> : ""}
            <nav className="nav mt-30 navHeader">
                <Link to="/" className="nav-link active"><i class="fa fa-home" aria-hidden="true"></i></Link>
                <Link to="/intro" className="nav-link">Giới Thiệu</Link>
                <Link to="/vi-tri" className="nav-link">Vị Trí</Link>    
                <Link to="/du-an" className="nav-link">Dự Án</Link>
                <Link to="/tien-ich" className="nav-link">Tiện Ích</Link>
                <Link to="/phat-trien-ben-vung" className="nav-link">Phát Triển Bền Vững</Link>
                <Link to="/tuyen-dung" className="nav-link">Tuyển Dụng</Link>
                <Link to="/tin-tuc" className="nav-link">Tin Tức</Link>
                <Link to="/hinh-anh" className="nav-link">Hình Ảnh</Link>
                <Link to="/lien-he" className="nav-link">Liên Hệ</Link>
            </nav>
        </div>
    );
}

export default NavHeader;