import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Footer from '../Footer/Footer';

export default class DuAn extends Component {
    render(){
        return(
            <div className="page page-duan">
                <div className="duan-house d-flex">
                    <div className="duan-house-text">
                        <Link to={'/du-an/nha-o'}><h3><b>BẤT ĐỘNG SẢN NHÀ Ở</b></h3></Link><br/>
                        <p>Thị trường bất động sản nhà ở tại TP HCM tiếp tục
                           có diễn biến tích cực, nguồn cung dồi dào và sức 
                           cầu của thị trường tốt. 
                        </p>
                        <br/>
                        <br/>
                        <Link to={'/du-an/nha-o'}><b>XEM THÊM <i class="fa fa-arrow-right" aria-hidden="true"></i></b></Link>  
                    </div>
                    <Link to={'/du-an/nha-o'}><img className="duan-house-img" src="http://zei.com.vn/wp-content/uploads/2019/09/chung-cu-the-zei-my-dinh.jpg"></img></Link>
                </div>
                <div className="duan-villa d-flex">
                    <Link to={'/du-an/villa'}><img className="duan-villa-img" src="https://www.takadada.com/wp-content/uploads/2019/07/hinh-nen-nha-dep-42.jpg"></img></Link>
                    <div className="duan-villa-text">
                        <Link to={'/du-an/villa'}><h3><b>BẤT ĐỘNG SẢN DU LỊCH NGHĨ DƯỠNG</b></h3></Link><br/>
                        <p>Các sản phẩm BĐS du lịch nghỉ dưỡng đa dạng, tọa lạc
                            tại các thành phố có tiềm năng du lịch lớn… chắc chắn
                            sẽ là lựa chọn hoàn hảo cho nhu cầu nghỉ dưỡng đẳng cấp
                            cũng như một kênh đầu tư sinh lời mạnh mẽ.
                        </p>
                        <br/>
                        <br/>
                        <Link to={'/du-an/villa'}><b>XEM THÊM <i class="fa fa-arrow-right" aria-hidden="true"></i></b></Link>
                    </div>
                </div>
                <Footer pageFooter={'duanFooter'}/>
            </div>
        );
    }
}