import React, { Component } from 'react';
import BackgroundSlider from 'react-background-slider';
import LibraryLink from '../Support/Library-Link';
import IntroButton from '../Support/Intro-Button';
import Footer from '../Footer/Footer';

export default class Home extends Component {
    render(){
        return(
            <div>
                <BackgroundSlider
                images={[
                    "https://chungcudep.net/wp-content/uploads/2015/11/phoi-canh-the-zei-mon-city-giai-doan-2-my-dinh.jpg", 
                    "https://heavenhomes.vn/wp-content/uploads/2020/12/khu-do-thi-mo.jpg",
                    "https://khudothixuanphuong.vn/library/module_new/hinh-nen_s682.png" ]}
                duration={10} transition={2} />
                <LibraryLink/>
                <IntroButton/>
                <div className="slideHome">
                    <h4 className="content-slide">CHIẾN LƯỢC PHÁT TRIỂN <br/>MỤC TIÊU </h4>
                    <div className="text-slide1">
                        <b>GIÁ TRỊ CỐT LÕI</b>
                        <ul className="text-slide-text">
                            <li>Hiệu quả</li>
                            <li>Chính trực</li>
                            <li>Chuyên nghiệp</li>
                        </ul>
                    </div>
                    <div className="text-slide2">
                        <b>TẦM NHÌN</b>
                        <ul className="text-slide-text">
                            <li>Bất động sản</li>
                            <li>Kiến tạo điểm đến du lịch</li>
                            <li>Phát triển hạ tầng giao thông</li>
                        </ul>
                    </div>
                    <div className="text-slide3">
                        <b>SỨ MẠNG</b>
                        <ul className="text-slide-text">
                            <li>Kiến tạo cộng đồng</li>
                            <li>Xây dựng điểm đến</li>
                            <li>Vun đắp niềm tin</li>
                        </ul>
                    </div>
                    <div className="d-flex text-content">
                        <i class="fa fa-arrow-circle-right icon-text-content" aria-hidden="true"></i>
                        <h6 className="text-content-text"><b>Slender Land</b> với dịch vụ tốt nhất cho ra những sản phẩm tốt nhất.</h6>
                    </div>
                </div>
                <img className="img-home2" src="http://cdn-img-v2.webbnc.net/uploadv2/web/13/13261/media/2019/11/21/07/14/1574320484_khach-bep-3.jpg"/>
                <Footer pageFooter={'homeFooter'}/>   
            </div>
        );
    }
}