import React, { Component } from 'react';
import IntroVideo from './intro-Video';

export default class IntroButton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShowVideo: false
        }
    }
    showVideo = () => {
        var { isShowVideo } = this.state;
        this.setState({ isShowVideo: !isShowVideo })
    }
    render() {
        var {isShowVideo} = this.state;
        return (
            <div>
                {isShowVideo ? <IntroVideo /> : ""}
                <div onClick={this.showVideo} className="btn-intro btn-support">
                    <i class="fa fa-play" aria-hidden="true"></i>
                    <p className="text-support ml-15">Video</p>
                </div>
            </div>
        );
    }
}